"use client";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { FaRegEdit } from "react-icons/fa";
import { MdDeleteOutline } from "react-icons/md";
export default function Users() {
  const route = useRouter();
  useEffect(()=>{
     const fetchdata=async()=>{
      await axio.get("http://localhost:5000/api/getall")
     }
   })
  return (
    <div className="flex justify-center items-center h-screen bg-gray-100 ">
      <div className="w-auto bg-white p-6 rounded-lg shadow-lg">
        <h1 className="text-2xl font-bold mb-4 text-center">CRUD APP</h1>

        <div className="flex justify-end mb-3">
          <button
            className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600"
            onClick={() => route.push("/adduser")}
          >
            Add User
          </button>
        </div>

        <table className="w-full border-collapse border border-gray-300">
          <thead>
            <tr className="bg-gray-200">
              <th className="border p-2">S.No.</th>
              <th className="border p-2">Name</th>
              <th className="border p-2">Email</th>
              <th className="border p-2">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td className="border p-2"></td>
              <td className="border p-2"></td>
              <td className="border p-2"></td>
              <td className="border p-2 flex space-x-2">
                <button
                  className="bg-green-500 text-white px-2 py-2 rounded-lg hover:bg-green-600"
                  onClick={() => route.push("/updateuser")}
                >
                  <FaRegEdit />
                </button>
                <button className="bg-red-500 text-white px-2 py-2 rounded-lg hover:bg-red-600">
                  <MdDeleteOutline />
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}
